import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
const elements = {
  input: document.querySelector('#datetime-picker'),
  startButton: document.querySelector('button[data-start]'),
  days: document.querySelector('[data-days]'),
  hours: document.querySelector('[data-hours]'),
  minutes: document.querySelector('[data-minutes]'),
  seconds: document.querySelector('[data-seconds]'),
};
elements.startButton.disabled = true;
let userSelectedDate = null;
const flatpickrOptions = {
  enableTime: true,
  time_24hr: true,
  defaultDate: new Date(),
  minuteIncrement: 1,
  onClose(selectedDates) {
    const selectedDate = selectedDates[0];
    if (selectedDate <= new Date()) {
      iziToast.error({
        title: 'Invalid Date',
        message: 'Please choose a date in the future',
        position: 'topRight',
      });
      elements.startButton.disabled = true;
    } else {
      userSelectedDate = selectedDate;
      elements.startButton.disabled = false;
    }
  },
};
flatpickr(elements.input, flatpickrOptions);
elements.startButton.addEventListener('click', () => {
  startTimer();
});
function startTimer() {
  elements.startButton.disabled = true;
  elements.input.disabled = true;
  const intervalId = setInterval(() => {
    const now = new Date();
    const timeLeft = userSelectedDate - now;
    if (timeLeft <= 0) {
      clearInterval(intervalId);
      updateTimerDisplay(0, 0, 0, 0);
      elements.input.disabled = false;
      iziToast.success({
        title: 'Time is Up!',
        message: 'The countdown has ended.',
        position: 'topRight',
      });
      return;
    }
    const time = convertMs(timeLeft);
    updateTimerDisplay(time.days, time.hours, time.minutes, time.seconds);
  }, 1000);
}
function convertMs(ms) {
  const second = 1000;
  const minute = second * 60;
  const hour = minute * 60;
  const day = hour * 24;
  const days = Math.floor(ms / day);
  const hours = Math.floor((ms % day) / hour);
  const minutes = Math.floor(((ms % day) % hour) / minute);
  const seconds = Math.floor((((ms % day) % hour) % minute) / second);
  return { days, hours, minutes, seconds };
}
function addLeadingZero(value) {
  return String(value).padStart(2, '0');
}
function updateTimerDisplay(days, hours, minutes, seconds) {
  elements.days.textContent = addLeadingZero(days);
  elements.hours.textContent = addLeadingZero(hours);
  elements.minutes.textContent = addLeadingZero(minutes);
  elements.seconds.textContent = addLeadingZero(seconds);
}
